import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ManageCountryCrudComponent } from './manage-country-crud/manage-country-crud.component';
import { TABS_DEMO_ROUTES } from './manage-country-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : '',component : ManageCountryCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
    
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class ManageCountryRoutingModule{}