import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { CountryService } from 'app/shared/services/geographics/country.service';
import { DatePipe } from '@angular/common';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { MessageConstant } from 'app/shared/utils/message.constant';


@Component({
  selector: 'app-action-dialog',
  templateUrl: './action-dialog.component.html',
  styleUrls: ['./action-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations,
  providers: [DatePipe]
})


export class ActionDialogComponent implements OnInit {

  params: any;
  isEditMode = false;
  form: FormGroup;
  localStorage: any;
  status = false;
  errorMsg: String;
  splitted: String;
  @ViewChild("toggleElement") ref: any;

  constructor(private fb: FormBuilder, private countryService: CountryService, private snackBar: MatSnackBar, private datePipe: DatePipe,
    public dialogRef: MatDialogRef<ActionDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: MtxDialogData) { }

  ngOnInit() {
    this.initialize();
  }

  initialize() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.params = this.data;
    this.isEditMode = this.params.action == 'CREATE' ? false : true;
    this.form = this.fb.group({
      name: ['', [Validators.required, Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      description: ['', [Validators.required, Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      iso_code: ['', [Validators.required, Validators.pattern(/^[a-zA-Z0-9/ ]*$/)]],
      country_code: ['', [Validators.required, Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      min_mobileLen:['',[Validators.required,Validators.pattern(/[0-9]/)]],
      max_mobileLen:['',[Validators.required,Validators.pattern(/[0-9]/)]],
      status: this.status == true ? 'Y' : 'N'
    });

    if (this.isEditMode) {
      this.assignValues(this.params);
    }
  }

  close() {
    this.dialogRef.close();
  }
  closeActionPanel() {
    this.dialogRef.close();
  }

  onYesClick() {
    if (this.form.valid) {
      let formValues = this.form.value;
      const params: any = {
        name: formValues.name,
        description: formValues.description,
        iso_code: formValues.iso_code,
        country_code: formValues.country_code,
        min_mobile_length: formValues.min_mobileLen,//10,
        max_mobile_length: formValues.max_mobileLen, //15,
        status: this.status == true ? 'Y' : 'N',
        modify_by: this.localStorage.userId
      }

      if (this.isEditMode) {
        params.id = this.params.data.id,
          params.modify_by = this.localStorage.userId;
      } else {
        params.created_by = this.localStorage.userId;
      }

      if (this.isEditMode) {
        this.countryService.update(params).subscribe(
          (countryData: any) => {
            if (countryData.status == true) {
              this.snackBar.open('Record updated successfully!', 'Dismiss', { duration: 5000 });
              this.dialogRef.close();
            } else {
              if (countryData.err_code == "ERROR : ER_DUP_ENTRY") {
                this.errorMsg = countryData.message;
                var index = this.errorMsg.lastIndexOf(" ");

                this.splitted = this.errorMsg.substring(index + 1);
                if (this.splitted == "'name'") {
                  this.snackBar.open(MessageConstant.nameDuplicateCountry, 'Dismiss', { duration: 5000 });
                } if (this.splitted == "'iso_code'") {
                  this.snackBar.open(MessageConstant.isoDuplicateCountry, 'Dismiss', { duration: 5000 });
                } if (this.splitted == "'country_code'") {
                  this.snackBar.open(MessageConstant.countryCodeDuplicateCountry, 'Dismiss', { duration: 5000 });
                }
              } else {
                this.snackBar.open(countryData.message, 'Dismiss', { duration: 5000 });
              }
              //this.dialogRef.close(); 
            }

          },
          (err) => {
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        )
      } else {
        this.countryService.create(params).subscribe(
          (data: any) => {
            this.snackBar.open('Rfid created!', 'Dismiss', { duration: 5000 });
            this.dialogRef.close();
          },
          (err) => {
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        )

      }

      this.dialogRef.close(params);
    } else {
      this.snackBar.open(MessageConstant.formInvalid, 'Dismiss', { duration: 2000 });
    }

  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  assignValues(params) {
    this.form.get('name').setValue(params.data.name);
    this.form.get('description').setValue(params.data.description);
    this.form.get('iso_code').setValue(params.data.iso_code);
    this.form.get('country_code').setValue(params.data.country_code);
    this.form.get('min_mobileLen').setValue(params.data.min_mobile_length);
    this.form.get('max_mobileLen').setValue(params.data.max_mobile_length);
    this.status = params.data.status == 'Y' ? true : false;
  }

  updateStatus(event) {
    this.status = event.checked;
    this.ref.checked = this.status;
  }

}





